svg{
	display: inline-block;
	vertical-align: middle;
	@include ba-transition('fill, stroke');
}
a,
[type="submit"],
[type="button"],
button{
	transition: color .3s ease-in-out;
}
.ba-button-mini svg{
	color: $white;
	&:hover{
		color: $primary-color;
		transform: scale(1.2);
		transition: color .3s ease-in-out, transform .3s ease-in-out;
	}
}
.menu .active>a{
	background: transparent;
	color: $primary-color;
	font-size: 30px;
	font-weight: 700;
}
.ba-intro{
	position: relative;
	background: #d688be url("../img/heroimage_x_2.jpg") no-repeat 50% / cover;
	padding-top: 28px;
	padding-bottom: 28px;
	color: $white;
	height: 100%;
	&__contacts	a:hover{
		transform: scale(1.2);
		transition: color .3s ease-in-out, transform .3s ease-in-out;
	}
	&__contacts-adress{
		font-style: normal;
		display: none;
		@include breakpoint (large){
			display: block;
		}
		svg{
			width: 30px;
			height: 30px;
		}
	}
	&__logo{
		display: flex;
		flex-direction: column;
		justify-content: center;
		height: 100%;
	}
	&__title{
		margin-bottom:50px;
	}
	img{
		transform: scale(0.6);
		@include breakpoint (medium){
			transform: scale(0.7);
		}
		@include breakpoint (large){
			transform: scale(1);
		}
	}
	&__menu{
		position: absolute;
		bottom: 0px;
		left: 0;
		right: 0;
		margin: 0 auto;
		display: flex;
		justify-content: center;
		a{
			padding: 0.7rem 1.5rem;
			@include breakpoint (large){

				padding: 0.7rem 3rem;
			}
		}
		a:hover{
			transform: scale(1.2);
			font-weight: 700;
			transition: color .3s ease-in-out, transform .3s ease-in-out;
		}
	}

	@include breakpoint (medium){
		height: 100vh;

	}
	@include breakpoint (large){
		padding-top: 90px;
		padding-bottom: 90px;
	}

	img{
		display: flex;
		margin: 0 auto;
	}

	&__contacts{
		display: flex;
		justify-content: space-between;
		letter-spacing: -0.02em;
	}
	a{
		color: $white;
	}
	a:hover{
		color: $primary-color;

	}
	&__contacts-social{
		display: flex;
		justify-content: center;
		padding-left: 20px;
		margin: 0 auto;
	}
	&__contacts-telnumber{
		display: none;
		justify-content: flex-end;
		font-style: normal;
		@include breakpoint (large){
			display: flex;
			justify-content: flex-end;
		}
	}
	&__title{
		text-align: center;
		font-size: 12px;
		@include breakpoint (medium){
			font-size: 22px;
		}
		@include breakpoint (large){
			font-size: 30px;
		}
	}
	.ba-nav{
		width: 100%;
		display: flex;
		justify-content: space-around;
	}
}
.ba-social-icon{
	margin-right: 30px;
}
.ba-container{
	height: 100%;
}
.ba-hidden-large{
	display: block;
	@include breakpoint (large){
		display: none;
	}
}
.ba-hidden-small{
	display: none;
	a:hover svg{
		transform: scale(1.2);
		transition: transform .3s ease-in-out;
	}
	@include breakpoint (large){
		display: block;

	}
}
/*__________section about ________________*/

.ba-about{
	position: relative;
	background: #eedbef url("../img/aboutusback_x2.jpg") no-repeat 50% / cover;
	padding: 21px 0 32px;
	font-size: 14px;
	color: #000000;
	z-index: 1;
	@include breakpoint (medium){
		padding: 60px 0 97px;
		font-size: 18px;
	}
	@include breakpoint (large) {
		padding: 65px 0 97px;
		font-size: 24px;
	}
	&__left-col{
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: 0 30px;
		z-index: 3;
		@include breakpoint (medium){
			padding-top: 55px;
			padding-left: 75px;
			padding-right: 75px
		}
		&:before{
			display: none;
			@include breakpoint (large){
				display: block;
				content: url("../img/frame_about.png");
				position: absolute;
				top: 58px;
				left: 10px;
				z-index: -1;
			}
		}
		&:after{
			display: none;
			@include breakpoint (large){
				display: block;
				content: url("../img/frame_about.png");
				position: absolute;
				transform: rotate(180deg);
				bottom: 20px;
				right: 5px;
				z-index: -1;
			}
		}
		p{
			line-height: 1.28;
			letter-spacing: -0.028em
		}
	}
	&__right-col{
		display: flex;
		padding-left: 42px;

	}
	&:after{
		position: absolute;
		bottom: -25px;
		left: calc(50% - 25px);
		content: url("../img/honeycomb-small.svg");
		width: 50px;
		height: 50px;
		@include breakpoint (medium){
			bottom: -37px;
			left: calc(50% - 37.5px);
			content: url("../img/honeycomb-medium.svg");
			width: 75px;
			height: 75px;
		}
		@include breakpoint (large){
			bottom: -51px;
			left: calc(50% - 37.5px);
			content: url("../img/honeycomb-large.svg");
			width: 102px;
			height: 102px;
		}
	}
}

/*__________section services ________________*/

.ba-services{
	background-color: #fbe2f4;
	padding-top: 48px;
	padding-bottom: 48px;
	text-align: center;
	position: relative;
	&__circle{
		//@include hide-for-only(small);
	}
	@include breakpoint (medium){
		padding-top: 131px;
		padding-bottom: 131px;
	}
	@include breakpoint (large){
		padding-top: 151px;
		padding-bottom: 151px;
	}
	&__col{
		margin-bottom: 30px;
		img{
			display: block;
			width: 165px;
			height: 165px;
			z-index: 1;

			@include breakpoint (medium){
				width: 195px;
				height: 195px;
			}
			@include breakpoint (large){
				width: 360px;
				height: 360px;
			}
		}
	}
}

/* ----------trade--------------- */

.ba-trade{
	background: #eedbef url("../img/buyback_x_2.jpg") no-repeat 50% / cover;
	padding-top: 35px;
	padding-bottom: 70px;
	font-size: 18px;
	position: relative;
	@include breakpoint (medium){
		padding-top: 45px;
		padding-bottom: 60px;
		font-size: 24px;
	}
	@include breakpoint (large){
		padding-top: 105px;
		padding-bottom: 156px;
		font-size: 30px;
	}
	p{
		margin-bottom: 10px;
		@include breakpoint (medium){
			margin-bottom: 30px;
		}
		@include breakpoint (large){
			margin-bottom: 115px;
		}
	}
	&__column-left{
		margin-bottom: 35px;
	}
	&__column-left, &__column-right{
	}
	&__img-left, &__img-right{
		max-width: 175px;
		z-index: 3;

		@include breakpoint (medium){
			max-width: 195px;
		}
		@include breakpoint (large){
			max-width: 360px;
		}
	}
	&__img-left{
		margin-left: auto;
	}
	&__img-left::before{
		position: absolute;
		content: url("../img/icon/ice-cream_2.svg");
		top: 0;
		left: -21%;
		z-index: -1;
	}
	&__img-right::before{
		position: absolute;
		content: url("../img/icon/ice-cream_1.svg");
		top: 0;
		left: -21%;
		z-index: -1;
	}

}

/*-------------------------Price ----------------------*/
.ba-price{
	position: relative;
	background: url('../img/pricesback_x_2.jpg') no-repeat 50% / cover;
	@include breakpoint (medium only){
		background: url('../img/pricegirl.png') no-repeat 50% 100%/ contain;
	}
	padding-top: 40px;
	padding-bottom: 40px;
	@include breakpoint (medium){
		padding-top: 20px;
		padding-bottom: 20px;
	}
	@include breakpoint (large){
		background: url('../img/pricegirl.png') no-repeat 5% 100%;
		padding-top: 65px;
		padding-bottom: 100px;
	}
	&__title{
		font-size: 18px;
		@include breakpoint (medium){
			font-size: 24px;
		}
		@include breakpoint (large){
			font-size: 30px;
		}
	}
	p{
		font-size: 14px;
		line-height: 1.43;
		margin-bottom: 5px;
		@include breakpoint (medium){
			font-size: 18px;
			line-height: 1.33;
		}
		@include breakpoint (large){
			font-size: 24px;
		}
		span{
			font-weight: bold;
		}
	}
	&__col-left{

	}
	&__col-right{
		@include breakpoint (medium only){
			text-align: right;
		}
	}
}


/*-----------------Footer---------------------------------*/

.ba-footer{
	background: url("../img/appointmentback_x_2.jpg") no-repeat 50% / cover;
	padding-top: 45px;
	padding-bottom: 45px;
	text-align: center;
	color: $white;
	position: relative;
	@include breakpoint (medium){
		padding-top: 60px;
		padding-bottom: 60px;
	}
	@include breakpoint (large){
		padding-top: 90px;
		padding-bottom: 90px;
	}
	svg{
		margin: 0 auto;
		width: 77px;
		height: 81px;
		margin-bottom: 25px;
		@include breakpoint (medium){
			width: 120px;
			height: 126px;
			margin-bottom: 25px;
		}
		@include breakpoint (large){
			width: 168px;
			height: 177px;
			margin-bottom: 30px;
		}
	}
	&__title{
		font-size: 36px;
		margin: 0;
		@include breakpoint (medium){
			font-size: 60px;
		}
		@include breakpoint (large){
			font-size: 120px;
		}
	}
	&__social-icon{
		margin-left: 25px;
		@include breakpoint (large){
			margin-left: 30px;
		}
		svg{
			width: 54px;
			height: 54px;
			margin-right: 25px;
			color: $white;
			@include breakpoint (large){
				width: 72px;
				height: 72px;
				margin-right: 30px;
			}
		}

	}
	&__tel{
		display: inline-block;
		color: $white;
		h2{
			font-size: 36px;
			@include breakpoint (medium){
				font-size: 60px;
			}
			@include breakpoint (large){
				font-size: 120px;
			}
		}

	}
	a:hover svg{
		color: #e0478a;
		transform: scale(1.2);
		transition: transform .3s ease-in-out;
	}
	&__copiright{
		background-color: #FBE2F4;
		color: #EEB0C9;
		text-align: center;
		padding-bottom: 50px;
		@include breakpoint (large){
			padding-bottom: 0;
		}

	}
}
.ba-footer-menu{
	background-color: #fff;
	bottom:0;
	position: fixed;
	z-index: 99999;
	width: 100%;
	text-align: center;
	padding: 5px 0 5px 45px;
	@include breakpoint (medium){
		padding-left: 90px;
	}

	svg{
		width: 40px;
		margin-right: 45px;
		fill: #EEB0C9;
		@include breakpoint (medium){
			margin-right: 90px;
		}
	}
	a:hover svg{
		fill: #e770a4;
	}
}

// ------------------------------------------------------------Study-------------------------------------------------
.ba-header{
	position: absolute;
	z-index: 5;
	width: 100%;
	background: linear-gradient( 0deg, rgba(238,176,201,0) 0%, rgb(238,176,201) 100%);

	ul{
		display: flex;
		justify-content: space-around;
		padding: 25px 0;
		margin-bottom: 0;
		list-style: none;
	}
	a{
		color: $white;
	}
	a:hover{
		color: rgb(224, 71, 138);
		transform: scale(1.25);
		transition: color .3s ease-in-out, transform .3s ease-in-out;

	}

}
/* ----------------------Google Maps------------------------- */

#ba-map{
	background: #fbe2f4;
	min-height: 350px;
	width: 100%;
	opacity: 0.6;
	@include breakpoint (medium down){
		min-height: 250px;
	}
}

/*-----------------After icon for section------------------------*/
.ba-about::after,
.ba-services::after,
.ba-trade::after,
.ba-price::after,{
	position: absolute;
	bottom: -25px;
	left: calc(50% - 25px);
	content: url("../img/honeycomb-small.svg");
	width: 50px;
	height: 50px;
	z-index: 3;
	@include breakpoint (medium){
		bottom: -37px;
		left: calc(50% - 37.5px);
		content: url("../img/honeycomb-medium.svg");
		width: 75px;
		height: 75px;
	}
	@include breakpoint (large){
		bottom: -51px;
		left: calc(50% - 37.5px);
		content: url("../img/honeycomb-large.svg");
		width: 102px;
		height: 102px;
	}
}
